import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { NavigatorProvider, TranslateProvider } from '@nequi/features/commons/providers';

import { ItsSeriousConfig } from './its-serious.config';

@Component({
  selector: 'app-credits-its-serious',
  templateUrl: './its-serious.page.html',
  styleUrls: ['./its-serious.page.scss'],
})
export class ItsSeriousPage implements OnInit {
  public config = ItsSeriousConfig;
  public itemList = [];

  constructor(
    private navigatorProvider: NavigatorProvider,
    private translateProvider: TranslateProvider,
    private route: ActivatedRoute
  ) {}

  public async ngOnInit() {
    this.itemList = this.translateProvider.get(this.config.i18n.itemList);
  }

  public goBack() {
    this.navigatorProvider.back();
  }

  public goToTermsAndConditions() {
    this.navigatorProvider.navigateForward(this.config.routes.termsAndConditions, {
      relativeTo: this.route,
    });
  }
}
