import { CurrencyPipe, DatePipe } from '@angular/common';
import { FormControl, Validators } from '@angular/forms';
import { RescheduleEntity } from '@nequi/features/credits/core';
import { Pocket } from '@nequi/features/money-management/core';
import { ConfirmInfoPocket, ConfirmInfoPocketItem } from '@nequi/ui';

import { ReschedulePaymentConfig } from './reschedule-payment.config';

export class ReschedulePaymentViewModel {
  public pocketList: Pocket[];
  public pocketControl: FormControl;
  public paymentAmount: number;
  public config = ReschedulePaymentConfig;
  public creditInfo: RescheduleEntity;

  constructor() {
    this.paymentAmount = 0;
    this.pocketControl = new FormControl(null, [Validators.required]);
  }

  public buildItems(
    messages: Record<string, any>,
    currencyPipe: CurrencyPipe,
    datePipe: DatePipe
  ): Array<ConfirmInfoPocketItem | ConfirmInfoPocket> {
    const { name, iconUrl, icon } = this.pocketControl.value;

    const pocket: ConfirmInfoPocket = {
      isPocket: true,
      title: messages.items.pocket,
      name: name,
      image: iconUrl,
      icon: icon,
    };

    return [
      {
        label: messages.items.currentInterests,
        value: currencyPipe.transform(this.creditInfo.normalInterest),
      },
      {
        label: messages.items.penaltyInterest,
        value: currencyPipe.transform(this.creditInfo.penaltyInterest),
      },
      {
        label: messages.items.lifeInsurance,
        value: currencyPipe.transform(this.creditInfo.feesOutstanding),
      },
      {
        label: messages.items.total,
        value: currencyPipe.transform(this.creditInfo.totalOtherValues),
      },
      {
        label: messages.items.newPaymentLimitDate,
        value: datePipe.transform(this.creditInfo.newLoan.endAt),
      },
      pocket,
    ];
  }
}
