import { Component, Injector, OnInit } from '@angular/core';
import { FormControl } from '@angular/forms';
import { ActivatedRoute } from '@angular/router';
import { CoreException } from '@nequi/commons/exceptions';
import { AnalyticsInteractor, AnalyticsType, Transaction } from '@nequi/features/commons/core';
import { TransactionBasePage } from '@nequi/features/commons/presentation';
import {
  CreditEventsHandler,
  CreditType,
  DisbursementEntity,
  DisbursementTransaction,
  OperationCreditType
} from '@nequi/features/credits/core';
import {
  AlertCategoryException,
  BlockingCategoryException,
  BreakCeilingException,
  DisbursementTimeOutException,
  WrongTimeDisbursementException
} from '@nequi/features/credits/core/exceptions';
import { CardModalService, IconToastType, ToastPosition } from '@nequi/ui';
import { getFormattedDateTimes } from '@nequi/utils/date';
import { stringsArrayToObjectWithValues } from '@nequi/utils/functions';

import { ContractType } from '../../../../organisms/contract/constants/contract-type';
import { CreditInfoModel } from '../../../../services/credit-info.model';
import { CustomerFinancialService } from '../../../../services/customer-finacial.service';
import { TermsAndConditionsConfig } from './terms-and-conditions.config';

@Component({
  selector: 'app-credits-terms-and-conditions',
  templateUrl: './terms-and-conditions.page.html',
  styleUrls: ['./terms-and-conditions.page.scss'],
})
export class TermsAndConditionsPage extends TransactionBasePage implements OnInit {
  public config = TermsAndConditionsConfig;
  public acceptAgreementCheckbox: FormControl;
  public termsAndConditions: string;
  public contractType = ContractType.CREDIT_LIFEGUARD;

  constructor(
    private route: ActivatedRoute,
    private creditInfoModel: CreditInfoModel,
    private customerFinancialService: CustomerFinancialService,
    private cardModalService: CardModalService,
    private creditEventsHandler: CreditEventsHandler,
    private analyticsInteractor: AnalyticsInteractor,
    injector: Injector
  ) {
    super(injector);
  }

  public ngOnInit(): void {
    this.setAnalyticsIntentionOfEnd();
  }

  public executeDisbursement() {
    const data: DisbursementEntity = {
      operatorType: OperationCreditType.DISBURSEMENT,
      isDebitType: true,
      approvedAt: this.creditInfoModel.creditFeatureInfo.approvedDate,
      loanValue: this.creditInfoModel.creditFeatureInfo.loanAmount,
      productId: CreditType.LIFEGUARD,
      gender: this.customerFinancialService.creditUserInfo.gender,
      purpose: this.creditInfoModel.creditFeatureInfo.loanDestination || '',
    };

    const transaction = new DisbursementTransaction<DisbursementEntity>().withData(data);

    super.executeTransaction(transaction);
  }

  protected async setOriginMoney(transaction: Transaction): Promise<Transaction> {
    transaction.withPocket(null);

    return transaction;
  }

  protected onTransactionSuccess() {
    this.disbursementSuccess();
  }

  protected async onTransactionFailed(error: CoreException) {
    await this.generalError(error);
  }

  private goToBackpack() {
    this.navigatorProvider.navigateForward(this.config.routes.backpack, {
      relativeTo: this.route,
    });
  }

  private goToRegistryFull() {
    this.navigatorProvider.navigateForward(this.config.routes.registryFull, {
      relativeTo: this.route,
    });
  }

  private async disbursementSuccess(): Promise<void> {
    await this.loadingService.hide(true);

    this.creditInfoModel.cleanPreapprovedInfo();

    this.creditEventsHandler.emitDisbursedEvent({
      creditType: CreditType.LIFEGUARD,
      hasCredits: true,
      isRescheduled: false,
      isExpired: false,
    });

    this.showCardModal(this.config.i18n.cardModalSuccess);
  }

  private async generalError(error: CoreException): Promise<void> {
    if (
      error.constructor === AlertCategoryException ||
      error.constructor === BlockingCategoryException
    ) {
      this.showCardModal(this.config.i18n.controlListError);

      return;
    }

    if (error.constructor === BreakCeilingException) {
      this.showBreakCeilingError();

      return;
    }

    if (error.constructor === DisbursementTimeOutException) {
      this.showDisbursementTimeOutError();

      return;
    }

    if (error.constructor === WrongTimeDisbursementException) {
      this.showWrongTimeDisbursementError(error);

      return;
    }

    await this.toastService.showWithIcon({
      message: error.message,
      type: IconToastType.ERROR,
      position: ToastPosition.TOP,
    });
  }

  private async showCardModal(i18nMessage: string): Promise<void> {
    const message = this.translateProvider.get(i18nMessage);

    await this.cardModalService.show({
      title: message.header,
      imgPath: message.image,
      content: message.content,
      buttons: [
        {
          text: message.buttons.accept,
          handler: () => {
            this.goToBackpack();
          },
        },
      ],
    });
  }

  private async showBreakCeilingError() {
    const message = this.translateProvider.get(this.config.i18n.breakCeilingError);

    await this.cardModalService.show({
      title: message.header,
      imgPath: message.image,
      content: message.content,

      buttons: [
        {
          text: message.buttons.breakCeiling,
          handler: () => this.goToRegistryFull(),
        },
        {
          color: this.config.colors.midnight,
          text: message.buttons.accept,
          handler: () => {
            this.goToBackpack();
          },
        },
      ],
    });
  }

  private async showDisbursementTimeOutError() {
    const message = this.translateProvider.get(this.config.i18n.disbursementTimeOutError);

    await this.cardModalService.show({
      title: message.header,
      imgPath: message.image,
      content: message.content,
      buttons: [
        {
          text: message.buttons.accept,
          handler: () => {
            this.creditInfoModel.cleanPreapprovedInfo();
            this.goToBackpack();
          },
        },
      ],
    });
  }

  private async showWrongTimeDisbursementError(error: CoreException) {
    const message = this.translateProvider.get(this.config.i18n.wrongTimeDisbursementError);

    this.alertService.show({
      messages: {
        header: message.header,
        message: error.message,
        buttons: message.buttons,
      },
      handlers: {
        accept: () => this.goToBackpack(),
      },
    });
  }

  private setAnalyticsIntentionOfEnd() {
    const installment = 1;
    const eventName = this.config.analytics.intentionOfEnd.name;

    this.analyticsInteractor.trackEvent({
      type: AnalyticsType.FACEBOOK,
      params: {
        eventName: eventName,
      },
    });

    this.analyticsInteractor.trackEvent({
      type: AnalyticsType.FIREBASE,
      params: { eventName: eventName },
    });

    this.analyticsInteractor.trackEvent({
      type: AnalyticsType.CLEVERTAP,
      params: {
        eventName: eventName,
        eventProps: stringsArrayToObjectWithValues(
          this.config.analytics.intentionOfEnd.params.cleverTap,
          [getFormattedDateTimes(), this.creditInfoModel.creditFeatureInfo.loanAmount, installment]
        ),
      },
    });
  }
}
