import { CurrencyPipe } from '@angular/common';
import {
  AfterViewChecked,
  ChangeDetectorRef,
  Component,
  EventEmitter,
  Input,
  OnInit,
  Output,
  ViewChild
} from '@angular/core';
import { NavigatorProvider, TranslateProvider } from '@nequi/features/commons/providers';
import { CreditEntity } from '@nequi/features/credits/core';
import { OriginMoneyService } from '@nequi/features/money-management/presentation';
import {
  OriginMoneyControlComponent
} from '@nequi/features/money-management/presentation/controls';
import { CurrencyConfigProvider } from '@nequi/ui';
import { createFormat } from '@nequi/utils/string';

import { LifeGuardConfig } from '../../pages/home/home.config';
import { CreditDetailConfig } from './payment.config';
import { PaymentViewModel } from './payment.view-model';

@Component({
  selector: 'app-payment-organism',
  templateUrl: './payment.organism.html',
  styleUrls: ['./payment.organism.scss'],
})
export class PaymentOrganism implements OnInit, AfterViewChecked {
  @ViewChild(OriginMoneyControlComponent) public originMoneyControl: OriginMoneyControlComponent;

  @Input() public data: CreditEntity;
  @Input() public organismId: string;
  @Output() public navigateTo = new EventEmitter();

  public messages = CreditDetailConfig;
  public viewModel: PaymentViewModel;
  public config = LifeGuardConfig;
  public replaceAmountText = '';

  private isTotalPayment = false;

  constructor(
    private navigatorProvider: NavigatorProvider,
    private readonly changeDetectorRef: ChangeDetectorRef,
    private translateProvider: TranslateProvider,
    private currencyPipe: CurrencyPipe,
    private currencyConfigProvider: CurrencyConfigProvider,
    private originMoneyService: OriginMoneyService
  ) {
    this.viewModel = new PaymentViewModel(currencyConfigProvider);
  }

  public ngAfterViewChecked() {
    this.changeDetectorRef.detectChanges();
  }

  public ngOnInit() {
    this.viewModel.initForm(this.data.balance, this.data.normalInterest, this.data.approvedValue);

    const message = this.translateProvider.get(this.messages.errors.invalidAmount);

    this.replaceAmountText = createFormat(message, [
      this.currencyPipe.transform(this.data.approvedValue),
    ]);

    this.setPocket();
  }

  public goBack() {
    this.navigatorProvider.back();
  }

  public async goToPayment() {
    this.isTotalPayment = parseFloat(this.viewModel.amount.value) === this.viewModel.totalAmount;

    this.navigateTo.emit({
      pocket: this.viewModel.pocket.value,
      amount: this.viewModel.amount.value,
      isTotalPayment: this.isTotalPayment,
    });
  }

  public goToDetail() {
    this.navigatorProvider.navigateForward(this.config.routes.paymentDetail, {
      state: this.data,
    });
  }

  private setPocket() {
    this.viewModel.pocket.setValue(this.originMoneyService.getAvailablePocket());
  }
}
