import { Component, Injector, ViewChild } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { FeatureBasePage } from '@nequi/features/commons/presentation';

import {
  ApprovalFrontOrganism
} from '../../../../organisms/approval-front/approval-front.organism';
import { KnowUsBetterEngineConfig } from './know-us-better-engine.config';

@Component({
  selector: 'app-credits-know-us-better-engine',
  templateUrl: './know-us-better-engine.page.html',
})
export class KnowUsBetterEnginePage extends FeatureBasePage {
  @ViewChild('nequiApprovalFrontModulesList')
  private approvalFrontOrganism: ApprovalFrontOrganism;

  public config = KnowUsBetterEngineConfig;

  constructor(private route: ActivatedRoute, injector: Injector) {
    super(injector);
  }

  public goBack() {
    this.navigatorProvider.back();
  }

  public onCompletedModuleData() {
    this.approvalFrontOrganism.finish();
  }

  public async onEngineCompleted(): Promise<void> {
    this.navigatorProvider.navigateRoot(this.config.routes.preapproved, {
      relativeTo: this.route,
      replaceUrl: true,
    });
  }
}
