import { Injectable } from '@angular/core';
import { AlertService } from '@nequi/features/commons/presentation';
import { NavigatorProvider, TranslateProvider } from '@nequi/features/commons/providers';
import {
  CardModalService,
  FirstTimeMessageBottomSheetService,
  ModalButton,
  ModalButtonColor,
  ToastService
} from '@nequi/ui';
import { openUrl } from '@nequi/utils/functions';

import { ModalParams } from '../../../../middlewares/modal.params';
import { LifeGuardConfig } from './home.config';

@Injectable()
export class HomeService {
  public config = LifeGuardConfig;

  constructor(
    private navigatorProvider: NavigatorProvider,
    private firstTimeMessageBottomSheetService: FirstTimeMessageBottomSheetService,
    private translateProvider: TranslateProvider,
    private cardModalService: CardModalService,
    private alertService: AlertService,
    private toastService: ToastService
  ) {}

  public async showPreapprovedWelcome() {
    const bottomSheetMessages = this.translateProvider.get(this.config.i18n.bottomSheet.welcome);

    const result = await this.firstTimeMessageBottomSheetService.show({
      icon: bottomSheetMessages.image,
      message: bottomSheetMessages.message,
      content: bottomSheetMessages.content,
      cssClass: this.config.bottomSheet.cssClass.firstTimeMessage,
      header: {
        view: true,
        action: () => this.firstTimeMessageBottomSheetService.hide(false),
      },
      button: {
        label: bottomSheetMessages.button,
        color: 'guayaba',
        fill: 'outline',
        action: () => this.firstTimeMessageBottomSheetService.hide(true),
      },
    });

    return result.data as boolean;
  }

  public async showCannotSaveYouCardModal() {
    const messages = this.translateProvider.get(this.config.i18n.cannotSaveYouAlert);

    await this.cardModalService.show({
      title: messages.title,
      imgPath: messages.image,
      content: messages.description,
      buttons: [
        {
          text: messages.buttons.accept,
        },
        {
          isLink: true,
          text: messages.link.text,
          handler: () => {
            openUrl(messages.link.url);
          },
        },
      ],
    });
  }

  public async showTwoButtonsModal(messages, url: string, withSecondButton = false) {
    await this.cardModalService.show({
      title: messages.title,
      imgPath: messages.image,
      content: messages.description,
      buttons: withSecondButton
        ? [
            {
              text: messages.buttons.accept,
              handler: () => this.navigatorProvider.navigateRoot(url),
            },
            {
              text: messages.buttons.cancel,
              color: ModalButtonColor.MIDNIGHT_GRAY,
            },
          ]
        : [
            {
              text: messages.buttons.accept,
            },
          ],
    });
  }

  public async showPendingPaymentProcessModal() {
    const messages = this.translateProvider.get(this.config.i18n.pendingPaymentProcess);

    await this.cardModalService.show({
      title: messages.title,
      imgPath: messages.image,
      content: messages.description,
      buttons: [
        {
          text: messages.buttons.accept,
        },
      ],
    });
  }

  public async showCombackLaterAlert(text: string, callback?: Function) {
    const i18n = this.translateProvider.get(this.config.i18n.alert.comeBackLater);

    i18n.message = text.replace(i18n.message, '').trim();

    await this.alertService.show({
      messages: {
        header: i18n.header,
        message: i18n.message,
        buttons: i18n.buttons,
      },
      handlers: {
        accept: () => {
          if (callback) {
            callback.bind(this);
          }
        },
      },
      backdropDismiss: false,
    });
  }

  public showDefaultAlert() {
    const message = {
      i18nKey: this.config.i18n.alert.defaultError,
      backdropDismiss: false,
    };

    this.alertService.showByI18n(message);
  }

  public async showToastService(message: string) {
    await this.toastService.showError(message);
  }

  public async showCardModal<T = void>(params: ModalParams): Promise<T> {
    const message = params.i18nKey ? this.translateProvider.get(params.i18nKey) : params.message;

    const buttons: ModalButton[] = [
      {
        text: message.buttons.accept,
        handler: async () => params.onAccept?.(),
      },
    ];

    if (message.buttons?.cancel) {
      buttons.push({
        text: message.buttons.cancel,
        color: ModalButtonColor.MIDNIGHT_GRAY,
        handler: async () => params.onCancel?.(),
      });
    }

    if (message.link) {
      buttons.push({
        isLink: true,
        text: message.link.text,
        handler: () => {
          openUrl(message.link.url);
        },
      });
    }

    const result = await this.cardModalService.show({
      title: message.title,
      imgPath: message.image,
      content: message.description,
      buttons: buttons,
    });

    return result?.data as Promise<T>;
  }
}
