import { FormControl, FormGroup, Validators } from '@angular/forms';
import { CurrencyConfigProvider } from '@nequi/ui';

import { UserPaymentAmountValidator } from '../../utils/user-payment-amount.validator';

export class PaymentViewModel {
  public totalAmount: number;
  public form: FormGroup;
  public amount: FormControl;
  public totalPayment: FormControl;
  public pocket = new FormControl();
  public decimalLimitValue: number;

  constructor(private currencyConfigProvider: CurrencyConfigProvider) {}

  public initForm(totalAmount: number, normalInterest: number, approvedValue: number) {
    this.totalAmount = totalAmount;
    this.amount = new FormControl({ value: totalAmount, disabled: true }, [
      Validators.required,
      Validators.min(1),
      Validators.max(totalAmount),
      UserPaymentAmountValidator.userPaymentAmountValidator(
        totalAmount,
        normalInterest,
        approvedValue
      ),
    ]);

    this.totalPayment = new FormControl(true, [Validators.required, Validators.requiredTrue]);

    this.decimalLimitValue = this.currencyConfigProvider.visualDecimalLimit;

    this.form = new FormGroup({
      pocket: this.pocket,
      amount: this.amount,
      total: this.totalPayment,
    });
  }

  public updateAmountStatus() {
    if (this.amount.disabled) {
      this.amount.setValue(null);
      this.amount.enable();
      this.decimalLimitValue = this.currencyConfigProvider.decimalLimit;
    } else {
      this.amount.setValue(this.totalAmount);
      this.amount.disable();
      this.decimalLimitValue = this.currencyConfigProvider.visualDecimalLimit;
    }

    this.form.markAsPristine();
    this.form.markAsUntouched();
  }
}
