import { CurrencyPipe, DatePipe } from '@angular/common';
import { Component, Injector, OnDestroy, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { CoreException, ExceedCeilingException } from '@nequi/commons/exceptions';
import {
  AnalyticsInteractor,
  AnalyticsType,
  NOT_BALANCE_AVAILABLE_ERRORS,
  Transaction
} from '@nequi/features/commons/core';
import { TransactionBasePage } from '@nequi/features/commons/presentation';
import {
  CreditEventsHandler,
  CreditType,
  OperationCreditType,
  PaymentEntity,
  PaymentTransaction,
  PaymentType,
  RescheduleType
} from '@nequi/features/credits/core';
import { PayCreditParam } from '@nequi/features/credits/core/params';
import { Pocket } from '@nequi/features/money-management/core';
import { CardModalService, ConfirmInfoBottomSheetService } from '@nequi/ui';
import { getFormattedDateTimes } from '@nequi/utils/date';
import { stringsArrayToObjectWithValues } from '@nequi/utils/functions';

import {
  ApprovalFrontViewModel
} from '../../../../organisms/approval-front/approval-front.view-model';
import { LifeGuardConfig } from './home.config';
import { HomeService } from './home.service';
import { HomeViewModel } from './home.view-model';

const TYPE_OF_NUMBER = 'number';

@Component({
  selector: 'app-credits-lifeguard',
  templateUrl: './home.page.html',
  styleUrls: ['./home.page.scss'],
  providers: [HomeService],
})
export class HomePage extends TransactionBasePage implements OnInit, OnDestroy {
  public config = LifeGuardConfig;
  public minAmount = 0;
  private pocket: Pocket;

  constructor(
    injector: Injector,
    public homeViewModel: HomeViewModel,
    protected confirmBottomSheetService: ConfirmInfoBottomSheetService,
    private route: ActivatedRoute,
    private currencyPipe: CurrencyPipe,
    private cardModalService: CardModalService,
    private datePipe: DatePipe,
    private creditEventsHandler: CreditEventsHandler,
    private approvalFrontViewModel: ApprovalFrontViewModel,
    private analyticsInteractor: AnalyticsInteractor
  ) {
    super(injector);
  }

  public async ngOnInit() {
    this.homeViewModel.initForm();
    this.setAnalyticsIntentionToPay();
  }

  public ngOnDestroy() {
    this.approvalFrontViewModel.resetValue();
  }

  public async navigateTo() {
    this.navigatorProvider.navigateForward(this.config.routes.paymentGuarantees, {
      relativeTo: this.route,
    });
  }

  public async processPayment(paymentInfo) {
    const messages = this.translateProvider.get(LifeGuardConfig.confirmation);

    this.homeViewModel.isTotalPayment = paymentInfo.isTotalPayment;
    this.homeViewModel.paymentAmount = paymentInfo.amount;
    this.pocket = paymentInfo.pocket;

    const { data } = await this.confirmBottomSheetService.show({
      title: messages.title,
      subtitle: messages.subtitle,
      items: this.homeViewModel.buildItems(
        messages,
        {
          payment: paymentInfo.amount,
          loan: this.homeViewModel.credit.balance,
          date: this.homeViewModel.credit.endAt.toString(),
          normalInterest: this.homeViewModel.credit.normalInterest,
          penaltyInterest: this.homeViewModel.credit.penaltyInterest,
          insurance: this.homeViewModel.credit.feesOutstanding,
          isTotalPayment: paymentInfo.isTotalPayment,
          isExpired: paymentInfo.isExpired,
        },
        paymentInfo.pocket,
        this.currencyPipe,
        this.datePipe
      ),
      confirmLabel: messages.button.next,
    });

    if (data === true) {
      this.makePayment();
    }
  }

  protected onTransactionSuccess(response: PaymentEntity) {
    if (this.homeViewModel.isTotalPayment) {
      this.creditEventsHandler.emitDisbursedEvent({
        creditType: CreditType.LIFEGUARD,
        hasCredits: false,
        isExpired: false,
        isRescheduled: false,
      });
    }

    this.showCardModal(response);
  }

  protected onTransactionFailed(error: CoreException) {
    this.setAnalyticsIntentionToPay(error);

    if (error.constructor === ExceedCeilingException) {
      throw error;
    }

    if (NOT_BALANCE_AVAILABLE_ERRORS.includes(error.code)) {
      throw error;
    }

    this.toastService.showError(error.message);
  }

  protected async isInsufficientMoney(transaction: Transaction): Promise<boolean> {
    if (typeof transaction.pocket?.amount === TYPE_OF_NUMBER) {
      return transaction.pocket?.amount < transaction.amount;
    }

    return false;
  }

  private async makePayment() {
    const data: PayCreditParam = {
      operatorType: OperationCreditType.PAYMENT,
      productId: CreditType.LIFEGUARD,
      debenture: this.homeViewModel.credit.debenture,
      paymentType: PaymentType.LOAN,
      rescheduleType: RescheduleType.INSTALLMENTS_NUMBER,
    };

    const transaction = new PaymentTransaction()
      .withData(data)
      .withAmount(this.homeViewModel.paymentAmount)
      .withPocket(this.pocket);

    super.executeTransaction(transaction);
  }

  private async showCardModal(data: PaymentEntity) {
    const messages = this.translateProvider.get(this.getI18Messages());

    this.cardModalService.show({
      imgPath: messages.icon,
      title: messages.title,
      content: messages.content,
      buttons: [
        {
          text: messages.buttons.next,
          handler: () => {
            if (this.homeViewModel.credit.isExpired) {
              this.navigatorProvider.navigateForward(LifeGuardConfig.routes.movements, {
                state: { tranId: data.transactionDetails.id },
              });

              return;
            }

            this.navigatorProvider.navigateRoot(LifeGuardConfig.routes.backpack);
          },
        },
      ],
    });
  }

  private getI18Messages() {
    if (this.homeViewModel.isTotalPayment) {
      return this.getTotalPaymentMessage();
    }

    return this.getPartialPaymentMessage();
  }

  private getTotalPaymentMessage() {
    return this.homeViewModel.credit.isExpired
      ? LifeGuardConfig.i18n.modal.totalPaymentExpired
      : LifeGuardConfig.i18n.modal.totalPayment;
  }

  private getPartialPaymentMessage() {
    return this.homeViewModel.credit.isExpired
      ? LifeGuardConfig.i18n.modal.partialPaymentExpired
      : LifeGuardConfig.i18n.modal.partialPayment;
  }

  private setAnalyticsIntentionToPay(error?: CoreException) {
    let eventName = this.config.analytics.intentionToPay.name.cleverTap;
    const nextInstallmentAt = this.homeViewModel.credit.installment.next.date;

    let params = this.config.analytics.intentionToPay.params.cleverTap;
    let paramsValue = [
      getFormattedDateTimes(),
      this.homeViewModel.credit.isExpired,
      getFormattedDateTimes(nextInstallmentAt),
      this.homeViewModel.credit.balance,
      this.homeViewModel.credit.installment.total - this.homeViewModel.credit.installment.totalPaid,
    ];

    if (error) {
      eventName = this.config.analytics.intentionToPay.error;
      params = params.concat(this.config.analytics.intentionToPay.params.error);
      paramsValue = paramsValue.concat([error.code, error.message]);
    }

    this.analyticsInteractor.trackEvent({
      type: AnalyticsType.CLEVERTAP,
      params: {
        eventName: eventName,
        eventProps: stringsArrayToObjectWithValues(params, paramsValue),
      },
    });
  }
}
