import { CurrencyPipe, DatePipe } from '@angular/common';
import { Injectable } from '@angular/core';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import {
  CreditEntity,
  PreapprovedEntity,
  ProductCommissionEntity
} from '@nequi/features/credits/core';
import { PresentationPocket } from '@nequi/features/money-management/presentation';
import { ConfirmInfoPocket, ConfirmInfoPocketItem } from '@nequi/ui';
import { createFormat } from '@nequi/utils/string';

import { LifeGuardConfig } from './home.config';

Injectable();
export class HomeViewModel {
  // TODO: get default country
  public form: FormGroup;
  public financingAmount: FormControl;
  public minAmount = 0;
  public maxAmount = 0;
  public credit: CreditEntity;
  public preapproved: PreapprovedEntity;
  public customerCredits;
  public isContentVisible = false;
  public monthlyInterestRate = 0;
  public insuranceCoverageAmount = 0;
  public loanAmount = 0;
  public isMinAmountPreaproved = false;
  public commission: ProductCommissionEntity;
  public preapprovedLabel = '';
  public expirtationDateText = '';
  public isWrongTime = false;
  public itemDetail = [];
  public pocket = new FormControl();
  public isTotalPayment = false;
  public paymentAmount = 0;
  public maxInstallments: number;

  public initForm() {
    this.financingAmount = new FormControl(0, [Validators.required]);
    this.form = new FormGroup({
      pocket: this.pocket,
      financingAmount: this.financingAmount,
    });
  }

  public buildItems(
    messages: Record<string, any>,
    values: Record<string, string | number>,
    pocketInfo: PresentationPocket,
    currencyPipe: CurrencyPipe,
    datePipe: DatePipe
  ): Array<ConfirmInfoPocketItem | ConfirmInfoPocket> {
    const pocket: ConfirmInfoPocket = {
      isPocket: true,
      title: messages.items.pocket,
      name: pocketInfo.name,
      image: pocketInfo.imageUrl,
      icon: pocketInfo.iconUrl,
    };

    let items: Array<ConfirmInfoPocketItem | ConfirmInfoPocket> = [
      {
        label: messages.items.amount,
        value: currencyPipe.transform(values.payment),
      },
      {
        label: messages.items.loan,
        value: currencyPipe.transform(values.loan),
      },
    ];

    if (values.isTotalPayment && !values.isExpired) {
      items = items.concat([
        {
          label: messages.items.normal,
          value: currencyPipe.transform(values.normalInterest),
        },
        {
          label: messages.items.insurance.label,
          value: currencyPipe.transform(values.insurance),
          helpMessage: createFormat(messages.items.insurance.message, [
            currencyPipe.transform(values.insurance),
          ]),
        },
      ]);
    }

    if (values.isExpired) {
      items = items.concat([
        {
          label: messages.items.interestArrears,
          value: currencyPipe.transform(values.penaltyInterest),
        },
        {
          label: messages.items.normal,
          value: currencyPipe.transform(values.normalInterest),
        },
        {
          label: messages.items.monthlyInsuranceToday.label,
          value: currencyPipe.transform(values.insurance),
          helpMessage: createFormat(messages.items.monthlyInsuranceToday.message, [
            currencyPipe.transform(LifeGuardConfig.insurance),
          ]),
        },
      ]);
    }

    items.push(
      {
        label: messages.items.date,
        value: datePipe.transform(new Date(values.date), LifeGuardConfig.finishDateFormat),
      },
      pocket
    );

    return items;
  }
}
