import { Component, Injector } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { FlowType } from '@nequi/features/commons/core';
import { FeatureBasePage } from '@nequi/features/commons/presentation';
import { UserInfoProvider } from '@nequi/features/commons/providers';

import { CreditLoggerService } from '../../../../services/credit-logger.service';
import { CustomerFinancialService } from '../../../../services/customer-finacial.service';
import { PaymentGuaranteesConfig } from './life-ensurance.config';

@Component({
  selector: 'app-credits-life-ensure',
  templateUrl: './life-ensurance.page.html',
  styleUrls: ['./life-ensurance.page.scss'],
})
export class LifeEnsurancePage extends FeatureBasePage {
  public config = PaymentGuaranteesConfig;

  constructor(
    injector: Injector,
    private route: ActivatedRoute,
    private userInfoProvider: UserInfoProvider,
    private customerFinancialService: CustomerFinancialService,
    private creditLoggerService: CreditLoggerService
  ) {
    super(injector);
  }

  public async goToLifeInsurance() {
    this.setLog(this.config.i18n.logs);

    const gender = this.userInfoProvider.getGender();

    const route = gender ? this.config.goTo.loan : this.config.goTo.gender;

    if (gender) {
      this.customerFinancialService.updateCreditUser({
        ...this.customerFinancialService.creditUserInfo,
        gender: gender,
      });
    }

    this.navigatorProvider.navigateForward(route, {
      relativeTo: this.route,
    });
  }

  private setLog(messages: string) {
    this.creditLoggerService.save({ flowType: FlowType.PROPELLER, i18nKey: messages });
  }
}
