import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { Gender } from '@nequi/features/commons/presentation';
import { NavigatorProvider } from '@nequi/features/commons/providers';

import { CustomerFinancialService } from '../../../../services/customer-finacial.service';
import { LifeInsuranceConfig } from './gender.config';
import { LifeInsuranceViewModel } from './gender.view-model';

@Component({
  selector: 'app-gender-credits',
  templateUrl: './gender.page.html',
  styleUrls: ['./gender.page.scss'],
})
export class GenderPage implements OnInit {
  public viewModel: LifeInsuranceViewModel;
  public config = LifeInsuranceConfig;
  public gender = Gender;

  constructor(
    private navigatorProvider: NavigatorProvider,
    private route: ActivatedRoute,
    private customerFinancialService: CustomerFinancialService
  ) {
    this.viewModel = new LifeInsuranceViewModel();
  }

  public ngOnInit() {
    this.initPage();
  }

  public goBack() {
    this.navigatorProvider.back();
  }

  public goToEnsureLoan() {
    this.customerFinancialService.updateCreditUser({
      ...this.customerFinancialService.creditUserInfo,
      gender: this.viewModel.gender.value,
    });

    this.navigatorProvider.navigateForward(this.config.goTo.ensureLoan, {
      relativeTo: this.route,
    });
  }

  private initPage(): void {
    this.viewModel.initForm();
  }
}
