import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { I18nModuleResolver } from '@nequi/features/commons';

import { LifeguardMiddleware } from '../../middlewares/lifeguard.middleware';
import { LifeGuardConfig } from './config/lifeguard.config';
import { EnsureLoanPage } from './pages/ensure-loan/ensure-loan.page';
import { GenderPage } from './pages/gender/gender.page';
import { HomePage } from './pages/home/home.page';
import { ItsSeriousPage } from './pages/its-serious/its-serious.page';
import { KnowUsBetterEnginePage } from './pages/know-us-better-engine/know-us-better-engine.page';
import { LifeEnsurancePage } from './pages/life-ensurance/life-ensurance.page';
import { LoanInfoPage } from './pages/loan-info/loan-info.page';
import { PaymentDetailPage } from './pages/payment-detail/payment-detail.page';
import { RescheduleHomePage } from './pages/reschedule-home/reschedule-home.page';
import { RescheduleInfoPage } from './pages/reschedule-info/reschedule-info.page';
import { ReschedulePaymentPage } from './pages/reschedule-payment/reschedule-payment.page';
import { TermsAndConditionsPage } from './pages/terms-and-conditions/terms-and-conditions.page';

const routes: Routes = [
  {
    path: '',
    component: HomePage,
    canActivate: [LifeguardMiddleware],
  },
  {
    path: 'its-serious',
    component: ItsSeriousPage,
  },
  {
    path: 'life-ensure',
    component: LifeEnsurancePage,
  },
  {
    path: 'gender',
    component: GenderPage,
  },
  {
    path: 'ensure-loan',
    component: EnsureLoanPage,
  },
  {
    path: 'loan-info',
    component: LoanInfoPage,
  },
  {
    path: 'terms-and-conditions',
    component: TermsAndConditionsPage,
  },
  {
    path: 'payment-detail',
    component: PaymentDetailPage,
  },
  {
    path: 'reschedule',
    component: RescheduleHomePage,
  },
  {
    path: 'reschedule-info',
    component: RescheduleInfoPage,
  },
  {
    path: 'reschedule-payment',
    component: ReschedulePaymentPage,
  },
  {
    path: 'know-us-better-engine',
    component: KnowUsBetterEnginePage,
  },
];

@NgModule({
  imports: [
    RouterModule.forChild(
      I18nModuleResolver.setup(routes, [
        LifeGuardConfig.i18n.moduleKey,
        LifeGuardConfig.i18n.creditsModuleKey,
        LifeGuardConfig.i18n.contractModuleKey,
      ])
    ),
  ],
  exports: [RouterModule],
})
export class LifeguardRoutingModule {}
