import { Component, Injector, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { ApplicationDataInteractor } from '@nequi/features/application-data/core';
import { FeatureBasePage, Gender } from '@nequi/features/commons/presentation';
import { ModuleConfigProvider, TranslateProvider } from '@nequi/features/commons/providers';
import {
  CreditInteractor,
  ScheduleName,
  ValidateLoanExtensionEntity,
  ValidateLoanRescheduleException
} from '@nequi/features/credits/core';
import { CardModalService, LoadingService } from '@nequi/ui';

import { RescheduleHomeConfig } from './reschedule-home.config';

@Component({
  selector: 'app-reschedule-home',
  templateUrl: './reschedule-home.page.html',
  styleUrls: ['./reschedule-home.page.scss'],
})
export class RescheduleHomePage extends FeatureBasePage implements OnInit {
  public config = RescheduleHomeConfig;
  public gender = Gender;
  public itemList = [];
  public showRetry = false;
  public loanAccountNumber: '';

  constructor(
    private translateProvider: TranslateProvider,
    private creditInteractor: CreditInteractor,
    private cardModalService: CardModalService,
    private loadService: LoadingService,
    private applicationDataInteractor: ApplicationDataInteractor,
    private moduleConfigProvider: ModuleConfigProvider,
    private router: Router,
    injector: Injector
  ) {
    super(injector);
  }

  public async ngOnInit() {
    if (this.router.getCurrentNavigation().extras.state) {
      this.loanAccountNumber = this.router.getCurrentNavigation().extras.state.loanAcc;

      if (!(await this.isProcessAlreadyStarted())) {
        await this.loadData();
      }
    }
  }

  public goBack() {
    this.navigatorProvider.back();
  }

  public async reload() {
    this.showRetry = false;
    await this.loadData();
  }

  public async loadData() {
    try {
      await this.loadService.show();
      const response = (await this.creditInteractor.validateLoanExtension(
        this.loanAccountNumber
      )) as ValidateLoanExtensionEntity;

      await this.loadService.hideWithoutFeedback();

      if (response.applyReschedule) {
        await this.validateReschedule(response.rescheduleName);
      }
    } catch (error) {
      if (error.constructor === ValidateLoanRescheduleException) {
        await this.showCardModal(this.config.i18n.modalsErrors[error.code]);

        return;
      }

      await this.loadService.hideWithoutFeedback();
      this.showRetry = true;
      throw error;
    }
  }

  public async goTo() {
    await this.applicationDataInteractor.save(
      this.moduleConfigProvider.applicationData.lifeguardRescheduleAlreadyStarted,
      true
    );

    this.navigatorProvider.navigateForward(this.config.routes.haveToPay);
  }

  private async isProcessAlreadyStarted() {
    const isAlreadyStarted = await this.applicationDataInteractor.findValue<boolean>(
      this.moduleConfigProvider.applicationData.lifeguardRescheduleAlreadyStarted,
      false
    );

    if (isAlreadyStarted) {
      setTimeout(() => {
        this.showCardModal(this.config.i18n.modalProcessIsAlreadyStarted);
      }, 0);

      return true;
    }

    return false;
  }

  private async validateReschedule(rescheduleName: string) {
    this.itemList = this.translateProvider.get(
      rescheduleName === ScheduleName.MODIFICATION
        ? this.config.i18n.modification
        : this.config.i18n.maintenance
    );
  }

  private async showCardModal(i18nMessages: string) {
    const messages = this.translateProvider.get(i18nMessages);
    let buttons = null;

    if (messages.buttons.cancel) {
      buttons = [
        {
          text: messages.buttons.next,
          handler: () => this.reload(),
        },
        {
          text: messages.buttons.cancel,
          color: 'medianoche',
          handler: () => this.navigatorProvider.navigateForward(this.config.routes.backpack),
        },
      ];
    }

    if (!messages.buttons.cancel) {
      buttons = [
        {
          text: messages.buttons.next,
          handler: () => this.navigatorProvider.navigateForward(this.config.routes.backpack),
        },
      ];
    }

    await this.cardModalService.show({
      title: messages.title,
      imgPath: messages.image,
      content: messages.content,
      buttons,
    });
  }
}
