import { CurrencyPipe } from '@angular/common';
import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { FlowType } from '@nequi/features/commons/core';
import { I18nMessages, NavigatorProvider } from '@nequi/features/commons/providers';
import { CreditInteractor, PreapprovedEntity } from '@nequi/features/credits/core';
import { ChipColor, ViewDetailStatus } from '@nequi/ui';
import { toCamelCaseProperties } from '@nequi/utils/functions';

import {
  ApprovalFrontViewModel
} from '../../../../organisms/approval-front/approval-front.view-model';
import { CreditFeatures, CreditInfoModel } from '../../../../services/credit-info.model';
import { CreditLoggerService } from '../../../../services/credit-logger.service';
import { LifeGuardConfig } from '../../pages/home/home.config';
import { HomeViewModel } from '../../pages/home/home.view-model';

@Component({
  selector: 'app-preapproved-organism',
  templateUrl: './preapproved.organism.html',
  styleUrls: ['./preapproved.organism.scss'],
})
export class PreApprovedOrganism implements OnInit {
  @Input() public messages: I18nMessages<string>;
  @Input() public data: PreapprovedEntity;
  @Input() public organismId: string;
  @Output() public navigateTo = new EventEmitter();

  public viewModel: HomeViewModel;
  public config = LifeGuardConfig;
  public detailColor = ViewDetailStatus.GRAY;
  public colorGreen = ChipColor.GREEN;
  public colorRed = ChipColor.RED;
  public details = [];
  public messagesMapped = null;

  constructor(
    private currencyPipe: CurrencyPipe,
    private creditInfoModel: CreditInfoModel,
    private navigatorProvider: NavigatorProvider,
    private creditInteractor: CreditInteractor,
    private creditLoggerService: CreditLoggerService,
    public approvalFrontViewModel: ApprovalFrontViewModel
  ) {
    this.viewModel = new HomeViewModel();
  }

  public async ngOnInit() {
    this.messagesMapped = toCamelCaseProperties(this.messages);
    this.viewModel.initForm();
    this.setViewModel(this.data);
    await this.loadDetails();
  }

  public goBack() {
    this.navigatorProvider.back();
  }

  public async goToPaymentGuarantees() {
    this.creditInfoModel.creditFeatureInfo = {
      loanAmount: this.viewModel.financingAmount.value,
      monthlyFee: this.getMonthlyFee(),
      installment: this.viewModel.maxInstallments,
      expirationDateText: this.viewModel.expirtationDateText,
      monthlyInterestRate: this.getMonthlyInterestRate(),
      commissionPercentage: this.viewModel.commission.percentage,
    };

    await this.registerAmountLog();

    this.navigateTo.emit();
  }

  public updateDetails() {
    const monthlyFee = this.getMonthlyFee();

    this.details[0].description = this.currencyPipe.transform(monthlyFee);
    this.details[1].description = this.currencyPipe.transform(
      this.viewModel.financingAmount.value + monthlyFee
    );
  }

  private async setViewModel(data: PreapprovedEntity) {
    this.creditInfoModel.creditFeatureInfo = {
      approveDate: this.data.approvedAt,
    } as CreditFeatures;

    this.viewModel.financingAmount.setValue(data.minAmount);
    this.viewModel.minAmount = data.minAmount;
    this.viewModel.maxAmount = data.maxAmount;
    this.viewModel.isMinAmountPreaproved = data.minAmount === data.maxAmount;
    this.viewModel.commission = data.product.commission;
    this.viewModel.maxInstallments = data.maxInstallment;
    this.viewModel.expirtationDateText = data.expirationDateText;

    this.viewModel.preapprovedLabel = !this.viewModel.isMinAmountPreaproved
      ? this.messagesMapped.amount.howMuch
      : this.messagesMapped.amount.min;

    this.approvalFrontIsCall();
  }

  private approvalFrontIsCall() {
    if (this.approvalFrontViewModel.info.isFromApproval) {
      const installmentList = this.approvalFrontViewModel.info.installmentList;

      if (installmentList?.length > 0) {
        this.viewModel.maxInstallments = installmentList[installmentList.length - 1].installment;
        this.viewModel.maxAmount = installmentList[installmentList.length - 1].value;
      }
    }
  }

  private async loadDetails() {
    const { label, how, when, help } = this.messagesMapped.monthlyPaymentAmount;

    this.details = [
      {
        label,
        helpMessage: help,
      },
      {
        label: how,
      },
      {
        label: when,
        description: this.data.expirationAt.toString(),
      },
    ];

    this.updateDetails();
  }

  private async registerAmountLog() {
    this.setLog(this.config.i18n.logs);
  }

  private getMonthlyFee(): number {
    return this.creditInteractor.getCommission({
      amount: this.viewModel.financingAmount.value,
      commission: this.viewModel.commission,
    });
  }

  private getMonthlyInterestRate(): number {
    const months = 12;
    const fee = 1;
    const p100 = 100;

    return (fee + this.viewModel.commission.percentage / p100) ** (fee / months) - fee;
  }

  private async setLog(messages) {
    this.creditLoggerService.save({ flowType: FlowType.PROPELLER, i18nKey: messages });
  }
}
