import { AbstractControl, ValidatorFn } from '@angular/forms';

export class UserPaymentAmountValidator {
  public static userPaymentAmountValidator(
    totalAmount: number,
    normalInterest: number,
    approvedValue: number
  ): ValidatorFn {
    return (control: AbstractControl): { [key: string]: any } | null => {
      if (totalAmount <= parseFloat(control.value)) {
        return null;
      }

      const isUserPaymentAmount = normalInterest ? control.value > approvedValue - 1 : false;

      return isUserPaymentAmount ? { userPaymentAmount: { value: control.value } } : null;
    };
  }
}
