import { CurrencyPipe } from '@angular/common';
import { Component, Injector, OnInit } from '@angular/core';
import { AlertService, FeatureBasePage } from '@nequi/features/commons/presentation';
import { TranslateProvider } from '@nequi/features/commons/providers';
import { CreditInteractor, RescheduleEntity } from '@nequi/features/credits/core';
import { IconToastType, ToastPosition, ToastService, ViewDetailItem } from '@nequi/ui';

import { RescheduleInfoConfig } from './reschedule-info.config';

@Component({
  selector: 'app-reschedule-info',
  templateUrl: './reschedule-info.page.html',
  styleUrls: ['./reschedule-info.page.scss'],
})
export class RescheduleInfoPage extends FeatureBasePage implements OnInit {
  public config = RescheduleInfoConfig;
  public showBusy: boolean;
  public reloadError: string;
  public detailItems: Array<ViewDetailItem>;
  public totalLoanAmount: number;
  public endAt: Date;
  public creditInfo: RescheduleEntity;

  constructor(
    private translateProvider: TranslateProvider,
    private toastService: ToastService,
    private alertService: AlertService,
    private creditInteractor: CreditInteractor,
    private currencyPipe: CurrencyPipe,
    injector: Injector
  ) {
    super(injector);
  }

  public async ngOnInit() {
    await this.getCreditDetail();
  }

  public async reload() {
    await this.getCreditDetail();
  }

  public goBack() {
    this.navigatorProvider.back();
  }

  public navigateToPayment() {
    this.navigatorProvider.navigateForward(this.config.routes.payment, {
      state: {
        ...this.creditInfo,
      },
    });
  }

  public async showInformationMessage() {
    const message = this.translateProvider.get(this.config.i18n.informationMessage);

    await this.toastService.showWithIcon({
      message,
      type: IconToastType.INFO,
      position: ToastPosition.TOP,
      duration: this.config.infoMessageDuration,
      isBackdropDismiss: true,
    });
  }

  private async getCreditDetail() {
    this.showBusy = true;
    this.reloadError = null;

    try {
      this.creditInfo = await this.creditInteractor.getInfoCredits(this.config.creditTypeShortTerm);

      if (!this.creditInfo) {
        await this.showExitAlert();
      } else {
        this.showBusy = false;

        this.showInformationMessage();
        this.loadCreditDetail(this.creditInfo);
      }
    } catch (error) {
      this.reloadError = this.translateProvider.get(this.config.i18n.retryMessage);
      await this.toastService.showError(error.message);
    }
  }

  private async loadCreditDetail(credit: RescheduleEntity) {
    this.totalLoanAmount = credit.balance;
    this.endAt = credit.newLoan.endAt;

    const items = this.translateProvider.get(this.config.i18n.interestsDetail);

    this.detailItems = [
      {
        label: items.commission.label,
        description: this.currencyPipe.transform(this.getCommissionValue(credit)),
        helpMessage: items.commission.message,
      },
      {
        label: items.lifeInsuranceInterest.label,
        description: this.currencyPipe.transform(credit.newLoan.totalInsurance),
        helpMessage: items.lifeInsuranceInterest.message,
      },
    ];
  }

  private async showExitAlert() {
    await this.alertService.showByI18n({
      i18nKey: this.config.i18n.exitAlert,
      handlers: {
        accept: () => {
          this.navigatorProvider.navigateRoot(this.config.routes.dashBoard);
        },
      },
      backdropDismiss: false,
    });
  }

  private getCommissionValue(credit: RescheduleEntity) {
    return this.creditInteractor.getCommission({
      amount: credit.approvedValue,
      commission: {
        maximum: null,
        percentage: credit.commission,
        value: null,
      },
    });
  }
}
