import { CurrencyPipe, DatePipe } from '@angular/common';
import { Component, Injector, OnInit, ViewChild } from '@angular/core';
import { Router } from '@angular/router';
import { CoreException, ExceedCeilingException } from '@nequi/commons/exceptions';
import { ApplicationDataInteractor } from '@nequi/features/application-data/core';
import { ModuleConfigProvider } from '@nequi/features/commons';
import { TransactionBasePage } from '@nequi/features/commons/presentation';
import {
  CreditEventsHandler,
  CreditType,
  LoanExtensionTimeoutFailException,
  OperationCreditType,
  PayCreditParam,
  PaymentTransaction,
  PaymentType,
  RescheduleEntity,
  RescheduleType
} from '@nequi/features/credits/core';
import { MoneyManagementInteractor } from '@nequi/features/money-management/core';
import { PocketMapper } from '@nequi/features/money-management/data';
import { OriginMoneyControlComponent } from '@nequi/features/money-management/presentation';
import { CardModalService, ConfirmInfoBottomSheetService, ViewDetailItem } from '@nequi/ui';
import { toCamelCaseProperties } from '@nequi/utils/functions';

import { ReschedulePaymentConfig } from './reschedule-payment.config';
import { ReschedulePaymentViewModel } from './reschedule-payment.view-model';

@Component({
  selector: 'app-reschedule-payment',
  templateUrl: './reschedule-payment.page.html',
  styleUrls: ['./reschedule-payment.page.scss'],
})
export class ReschedulePaymentPage extends TransactionBasePage implements OnInit {
  @ViewChild(OriginMoneyControlComponent)
  public originMoneyControl: OriginMoneyControlComponent;

  public config = ReschedulePaymentConfig;
  public detailItems: Array<ViewDetailItem>;
  public viewModel: ReschedulePaymentViewModel;
  public i18n: Record<string, any>;

  constructor(
    private router: Router,
    private currencyPipe: CurrencyPipe,
    private datePipe: DatePipe,
    protected confirmBottomSheetService: ConfirmInfoBottomSheetService,
    private moneyManagementInteractor: MoneyManagementInteractor,
    private applicationDataInteractor: ApplicationDataInteractor,
    private cardModalService: CardModalService,
    private creditEventsHandler: CreditEventsHandler,
    private moduleConfigProvider: ModuleConfigProvider,
    injector: Injector
  ) {
    super(injector);

    this.viewModel = new ReschedulePaymentViewModel();

    this.initI18n();
    this.loadDetailItems();
  }

  public ngOnInit() {
    this.viewModel.creditInfo = this.router.getCurrentNavigation().extras
      ?.state as RescheduleEntity;
  }

  public goBack() {
    this.navigatorProvider.back();
  }

  public onPocketSelected(pocket) {
    this.viewModel.pocketControl.setValue(pocket);
    this.showConfirmationInfo();
  }

  public async showPockets() {
    try {
      await this.loadingService.show();
      await this.loadPockets();
      await this.loadingService.hide(true);
      this.originMoneyControl.selectOriginMoney();
    } catch (error) {
      await this.loadingService.hide(false);
      await this.toastService.showError(error.message);
    }
  }

  protected async onTransactionSuccess() {
    try {
      await this.applicationDataInteractor.save(
        this.moduleConfigProvider.applicationData.lifeguardRescheduleAlreadyStarted,
        false
      );

      this.creditEventsHandler.emitDisbursedEvent({
        creditType: CreditType.LIFEGUARD,
        hasCredits: true,
        isExpired: false,
        isRescheduled: true,
      });

      this.showSuccessModal(this.i18n.cardModal.raiseAnchor);
    } catch (error) {
      this.toastService.showError(error.message);
    }
  }

  protected onTransactionFailed(error: CoreException): void {
    if (error.constructor === ExceedCeilingException) {
      throw error;
    }

    if (error instanceof LoanExtensionTimeoutFailException) {
      this.showExceptionModal(this.i18n.cardModal.manDrift);
    } else {
      this.toastService.showError(error.message);
    }
  }

  private async showSuccessModal(modal: Record<string, string>) {
    const newLimitDateFormatted = this.datePipe.transform(this.viewModel.creditInfo.newLoan.endAt);

    await this.cardModalService.show({
      title: modal.title,
      imgPath: modal.image,
      content: this.translateProvider.get(this.config.i18n.raiseAnchorContent, {
        newLimitDate: newLimitDateFormatted,
      }),
      buttons: [
        {
          text: modal.readyButton,
          handler: () => this.navigatorProvider.navigateRoot(this.config.routes.dashboard),
        },
      ],
    });
  }

  private async showExceptionModal(modal: Record<string, string>) {
    await this.cardModalService.show({
      title: modal.title,
      imgPath: modal.image,
      content: modal.content,
      buttons: [
        {
          text: modal.readyButton,
          handler: () => this.navigatorProvider.navigateRoot(this.config.routes.dashboard),
        },
      ],
    });
  }

  private async loadPockets(): Promise<void> {
    const pockets = await this.moneyManagementInteractor.getPocketsForTransaction();

    this.viewModel.pocketList = new PocketMapper().fromList(pockets);
  }

  private async showConfirmationInfo(): Promise<void> {
    const { data } = await this.confirmBottomSheetService.show({
      title: this.i18n.confirmation.title,
      subtitle: this.i18n.confirmation.subtitle,
      items: this.viewModel.buildItems(this.i18n.confirmation, this.currencyPipe, this.datePipe),
      confirmLabel: this.i18n.confirmation.button,
    });

    if (data === true) {
      this.makePayment();
    }
  }

  private async makePayment(): Promise<void> {
    const data: PayCreditParam = {
      operatorType: OperationCreditType.PAYMENT,
      productId: CreditType.LIFEGUARD,
      debenture: this.viewModel.creditInfo.loanAccount,
      paymentType: PaymentType.LOAN_RESCHEDULE,
      rescheduleType: RescheduleType.INSTALLMENTS_NUMBER,
    };

    const transaction = new PaymentTransaction()
      .withData(data)
      .withAmount(this.viewModel.creditInfo.totalOtherValues)
      .withPocket(this.viewModel.pocketControl.value);

    super.executeTransaction(transaction);
  }

  private async initI18n(): Promise<void> {
    const result = this.translateProvider.get(this.config.i18n.content);

    this.i18n = toCamelCaseProperties(result);
  }

  private async loadDetailItems() {
    const items = this.translateProvider.get(this.config.i18n.detailItems);

    this.detailItems = [
      {
        label: items.currentInterests.label,
        description: this.currencyPipe.transform(this.viewModel.creditInfo.normalInterest),
        helpMessage: items.currentInterests.help,
      },
      {
        label: items.penaltyInterest.label,
        description: this.currencyPipe.transform(this.viewModel.creditInfo.penaltyInterest),
        helpMessage: items.penaltyInterest.help,
      },
      {
        label: items.lifeInsurance.label,
        description: this.currencyPipe.transform(this.viewModel.creditInfo.feesOutstanding),
        helpMessage: items.lifeInsurance.help,
      },
      {
        label: items.total,
        description: this.currencyPipe.transform(this.viewModel.creditInfo.totalOtherValues),
      },
    ];
  }
}
