import { Injectable } from '@angular/core';
import {
  ActivatedRoute,
  ActivatedRouteSnapshot,
  CanActivate,
  RouterStateSnapshot
} from '@angular/router';
import { CoreException } from '@nequi/commons/exceptions';
import { AnalyticsInteractor, AnalyticsType, FlowType } from '@nequi/features/commons/core';
import { AlertService } from '@nequi/features/commons/presentation';
import {
  ModuleConfigProvider,
  NavigatorProvider,
  TranslateProvider
} from '@nequi/features/commons/providers';
import {
  CreditEventsHandler,
  CreditInteractor,
  CreditStatus,
  CreditType,
  DontHaveApprovalAttemptException,
  DontHaveApprovalAttemptWithDataException,
  ErrorCode,
  GetApprovalAttemptEntity,
  HaveApprovalAttemptException,
  PreapprovedEntity,
  UnavailabilityTimeException
} from '@nequi/features/credits/core';
import { GetCustomerCreditResponse } from '@nequi/mfp/adapters/credits-services/entities';
import { FullscreenModalService, LoadingService } from '@nequi/ui';
import { getFormattedDateTimes } from '@nequi/utils/date';
import { stringsArrayToObjectWithValues, toCamelCaseProperties } from '@nequi/utils/functions';

import { ApprovalFrontViewModel } from '../organisms/approval-front/approval-front.view-model';
import { LifeGuardConfig } from '../products/lifeguard/pages/home/home.config';
import { HomeService } from '../products/lifeguard/pages/home/home.service';
import { HomeViewModel } from '../products/lifeguard/pages/home/home.view-model';
import { Agreement, CreditInfoModel } from '../services/credit-info.model';
import { CreditLoggerService } from '../services/credit-logger.service';
import { ModalParams } from './modal.params';

@Injectable()
export class LifeguardMiddleware implements CanActivate {
  public factoryException: Record<string, (error: CoreException) => Promise<void | boolean>>;
  private config = LifeGuardConfig;

  constructor(
    private creditInteractor: CreditInteractor,
    private homeService: HomeService,
    private loadingService: LoadingService,
    private translateProvider: TranslateProvider,
    private homeViewModel: HomeViewModel,
    private creditLoggerService: CreditLoggerService,
    private creditInfoModel: CreditInfoModel,
    private creditEventsHandler: CreditEventsHandler,
    private fullscreenModalService: FullscreenModalService,
    private navigatorProvider: NavigatorProvider,
    private route: ActivatedRoute,
    private approvalFrontViewModel: ApprovalFrontViewModel,
    private moduleConfigProvider: ModuleConfigProvider,
    private alertService: AlertService,
    private analyticsInteractor: AnalyticsInteractor
  ) {}

  public async canActivate(_route: ActivatedRouteSnapshot, _state: RouterStateSnapshot) {
    await this.translateProvider.loadModule(this.moduleConfigProvider.i18n.moduleKey);
    await this.translateProvider.loadModule(this.moduleConfigProvider.i18n.creditsModuleKey);

    if (!this.approvalFrontViewModel.info.isFromApproval) {
      this.setExceptionFactory();

      return this.getCustomerCreditInfo();
    }

    return true;
  }

  private async getCustomerCreditInfo() {
    try {
      await this.loadingService.show();
      const lifeguard = await this.creditInteractor.getLifeguard();

      await this.loadingService.hideWithoutFeedback();

      if (lifeguard.credit) {
        if (lifeguard.credit.status === CreditStatus.PAID) {
          await this.homeService.showPendingPaymentProcessModal();

          return false;
        }

        this.homeViewModel.credit = lifeguard.credit;
        this.homeViewModel.credit.endAt.setMinutes(
          this.homeViewModel.credit.endAt.getMinutes() +
            this.homeViewModel.credit.endAt.getTimezoneOffset()
        );

        return true;
      }

      return this.processPreapproved(lifeguard.preapproved);
    } catch (error) {
      await this.loadingService.hideWithoutFeedback();

      return this.processErrors(error);
    }
  }

  private async processErrors(error: CoreException<GetCustomerCreditResponse>) {
    if (this.factoryException[error.code]) {
      return !!(await this.factoryException[error.code](error)) ?? false;
    }

    if (error.constructor === UnavailabilityTimeException) {
      await this.showPromptAlertUnavailabilityTime(error);

      return;
    }

    this.homeService.showDefaultAlert();

    return false;
  }

  private async processPreapproved(preapproved: PreapprovedEntity) {
    if (!preapproved) {
      await this.homeService.showCannotSaveYouCardModal();

      return;
    }

    this.homeViewModel.preapproved = preapproved;
    this.creditInfoModel.agreement = {
      agreement: preapproved?.product.agreement,
    } as Agreement;

    const result = await this.homeService.showPreapprovedWelcome();

    if (result && !this.homeViewModel.isWrongTime) {
      await this.setAnalyticsIntentionOfBegin();
      await this.registerWelcomeLog();

      return true;
    }

    return false;
  }

  private async processPersonalDataError() {
    const messages = this.translateProvider.get(LifeGuardConfig.i18n.alert.wrongPersonalData);

    await this.homeService.showTwoButtonsModal(messages, '');
  }

  private async processPenaltyForTime(message: string) {
    const messages = this.translateProvider.get(LifeGuardConfig.i18n.alert.penaltyForTime);

    messages.description = message;

    await this.homeService.showTwoButtonsModal(messages, '');
  }

  private async registerWelcomeLog() {
    this.setLog(LifeGuardConfig.i18n.logs);
  }

  private async setLog(messages: string) {
    this.creditLoggerService.save({ flowType: FlowType.LIFEGUARD, i18nKey: messages });
  }

  private setExceptionFactory() {
    this.factoryException = {
      [ErrorCode.USER_DONT_HAVE_PREAPPROVED]: (error: CoreException) =>
        this.customerMustGoToEngine(error),
      [ErrorCode.USER_DONT_HAVE_PREAPPROVED_CREDIT_REQUESTED]: (error: CoreException) =>
        this.customerMustGoToEngine(error),
      [ErrorCode.ISNT_POSSIBLE]: async (error: CoreException) => {
        await this.exceptionEmitDisbursedEvent(error);
      },
      [ErrorCode.USER_DONT_HAVE_CREDITS]: async (error: CoreException) => {
        await this.exceptionEmitDisbursedEvent(error);
      },
      [ErrorCode.PENALTY_FOR_TIME]: async (error: CoreException) => {
        await this.processPenaltyForTime(error.message);
      },
      [ErrorCode.WRONG_DOCUMENT_ID_EXPIRIAN]: async () => {
        await this.processPersonalDataError();
      },
      [ErrorCode.WRONG_DOCUMENT_TYPE_ID_EXPIRIAN]: async () => {
        await this.processPersonalDataError();
      },
      [ErrorCode.WRONG_LAST_NAME_EXPIRIAN]: async () => {
        await this.processPersonalDataError();
      },
    };
  }

  private async showPromptAlertUnavailabilityTime(error: CoreException) {
    const i18n = this.translateProvider.get(this.config.i18n.alert.unavailabilityTime.alertconfig);

    const messages = {
      header: i18n.title,
      message: error.message,
      buttons: {
        accept: i18n.button,
      },
    };

    this.alertService.show({
      messages,
      handlers: {
        accept: () => this.navigatorProvider.navigateRoot(this.config.routes.dashboard),
      },
    });
  }

  private async customerMustGoToEngine(error): Promise<boolean> {
    let result = false;

    if (error?.data) {
      result = await this.processPreapproved(error.data?.preapproved);
    }

    return result ? this.showUserDontHavePreapprovalModal() : result;
  }

  private async exceptionEmitDisbursedEvent(error) {
    this.creditEventsHandler.emitDisbursedEvent({
      creditType: CreditType.LIFEGUARD,
      hasCredits: error.data?.preapproved?.length > 0 || error.data?.product?.length > 0,
      isExpired: false,
      isRescheduled: false,
    });

    await this.homeService.showCannotSaveYouCardModal();
  }

  // TODO: mover a organismo y hacerlo general
  private async showUserDontHavePreapprovalModal(): Promise<boolean> {
    const i18n = this.translateProvider.get(this.config.i18n.modal.dontHavePreapproved);
    const i18nFormatted = toCamelCaseProperties<Record<string, string>>(i18n);

    const result = await this.fullscreenModalService.show<{ data: boolean }>({
      title: i18nFormatted.title,
      imgPath: i18nFormatted.image,
      content: i18nFormatted.description,
      cssClass: this.config.fullscreenModal.cssClass,
      buttons: [
        {
          text: i18nFormatted.acceptButton,
          handler: () => this.getApprovalUpFrontAttempt(),
          cssClass: this.config.fullscreenModal.cssClassButton,
        },
      ],
      link: {
        text: i18nFormatted.moreInfoLink,
        url: this.config.routes.moreInfo,
      },
    });

    return !!result?.data;
  }

  private async getApprovalUpFrontAttempt(): Promise<boolean> {
    try {
      await this.loadingService.show();
      await this.creditInteractor.getApprovalAttempt();

      await this.loadingService.hideWithoutFeedback();

      this.navigatorProvider.navigateForward(this.config.routes.knowUsBetterEngine, {
        relativeTo: this.route,
      });
    } catch (error) {
      await this.loadingService.hideWithoutFeedback();

      if (
        error.constructor === DontHaveApprovalAttemptException ||
        error.constructor === DontHaveApprovalAttemptWithDataException
      ) {
        this.processGetApprovalAttemptError(error);

        return false;
      }

      if (error.constructor !== HaveApprovalAttemptException) {
        await this.homeService.showToastService(error.message);

        return true;
      }

      if (error.constructor === HaveApprovalAttemptException) {
        this.navigatorProvider.navigateForward(this.config.routes.knowUsBetterEngine, {
          relativeTo: this.route,
        });
      }
    }
  }

  private async processGetApprovalAttemptError(error: CoreException) {
    const messages = this.translateProvider.get(this.config.i18n.modal.dontHaveApprovalAttempt);

    messages.description = this.translateProvider.get(
      this.config.i18n.modal.dontHaveApprovalAttemptDescription,
      {
        limitedConsultations: String((error.data as GetApprovalAttemptEntity).attempts.maxAttempt),
      }
    );

    this.homeService.showCardModal({ message: messages } as ModalParams);
  }

  private setAnalyticsIntentionOfBegin() {
    const eventName = this.config.analytics.intentionOfBegin.name;

    this.analyticsInteractor.trackEvent({
      type: AnalyticsType.FACEBOOK,
      params: {
        eventName: eventName,
      },
    });

    this.analyticsInteractor.trackEvent({
      type: AnalyticsType.FIREBASE,
      params: { eventName: eventName },
    });

    this.analyticsInteractor.trackEvent({
      type: AnalyticsType.CLEVERTAP,
      params: {
        eventName: eventName,
        eventProps: stringsArrayToObjectWithValues(
          this.config.analytics.intentionOfBegin.params.cleverTap,
          [getFormattedDateTimes(), String(!!this.homeViewModel.preapproved)]
        ),
      },
    });
  }
}
